/* --- ANIMATIONS --- */

$base-duration: 600ms;
$base-ease: cubic-bezier(0.25, 0.46, 0.45, 0.84);

/* --- HOME --- */
@keyframes home-element-in {
  0% {
    opacity: 0;
    transform: translateY(50%);
  }
  50% {
    opacity: 0.6;
    transform: translateY(0);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes home-element-left {
  0% {
    opacity: 0;
    transform: translateX(50%);
  }
  50% {
    opacity: 0.6;
    transform: translateX(0);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes home-element-rigth {
  0% {
    opacity: 0;
    transform: translateX(-50%);
  }
  50% {
    opacity: 0.6;
    transform: translateX(0);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

/* -- BIO --- */
@keyframes bio-element-left {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%);
  }
  50% {
    opacity: 0.6;
    transform: translate(0, -50%);
  }
  100% {
    opacity: 1;
    transform: translate(0, -50%);
  }
}

@keyframes text-scroll {
  0% {
    height: 0;
    opacity: 0;
  }
  100% {
    height: 102px;
    opacity: 1;
  }
}

@keyframes text-scroll-line {
  0% {
    height: 0;
    opacity: 0;
  }
  100% {
    height: 100vh;
    opacity: 1;
  }
}


/* --- EVENT --- */
@keyframes event-next {
  0% {
    left: 650px;
    transform: scale(0.9);
    opacity: 0.7;
  }
  100% {
    left: 0;
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes event-next-sibling {
  0% {
    left: 100%;
    transform: scale(1);
    opacity: 0;
  }
  100% {
    left: 650px;
    transform: scale(0.9);
    opacity: 0.7;
  }
}

@keyframes event-prev {
  0% {
    left: -100%;
    transform: scale(0.9);
    opacity: 0.7;
  }
  100% {
    left: 0;
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes event-prev-sibling {
  0% {
    left: 0;
    transform: scale(1);
    opacity: 0;
  }
  100% {
    left: 650px;
    transform: scale(0.9);
    opacity: 0.7;
  }
}

/**
 * ----------------------------------------
 * animation fade-in-bottom
 * ----------------------------------------
 */
 @keyframes fade-in-bottom {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fade-in-bottom-o {
  0% {
    transform: translateY(50px);
  }
  100% {
    transform: translateY(0);
  }
}



/**
 * ----------------------------------------
 * animation slide-in-bck-center
 * ----------------------------------------
 */
 @keyframes slide-in-bck-center {
  0% {
    transform: translateZ(600px);
    opacity: 0;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}

/**
 * ----------------------------------------
 * animation slide-in-right
 * ----------------------------------------
 */
 @keyframes slide-in-right {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slide-in-right-x {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/**
 * ----------------------------------------
 * animation slide-in-left
 * ----------------------------------------
 */
 @keyframes slide-in-left {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slide-in-left-x {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/**
 * ----------------------------------------
 * animation slide-in-blurred-right
 * ----------------------------------------
 */
 @keyframes slide-in-blurred-right {
  0% {
    transform: translateX(1000px) scaleX(2.5) scaleY(0.2);
    transform-origin: 0% 50%;
    filter: blur(40px);
    opacity: 0;
  }
  100% {
    transform: translateX(0) scaleY(1) scaleX(1);
    transform-origin: 50% 50%;
    filter: blur(0);
    opacity: 1;
  }
}